import React from 'react';
import { FeatureBox } from '../../components';

export function FeaturesBox(props) {
    return (
        <section className="section-features">
               

                <div className="row">
                    <div className="col-1-of-4">
                        <FeatureBox icon_class="icon-basic-world" title="Explore the world" />
                    </div>
                    <div className="col-1-of-4">
                        <FeatureBox icon_class="icon-basic-compass" title="Meet Nature" />
                    </div>
                    <div className="col-1-of-4">
                        <FeatureBox icon_class="icon-basic-map" title="Find your way" />
                    </div>
                    <div className="col-1-of-4">
                        <FeatureBox icon_class="icon-basic-heart" title="Live a healthier life"/>
                    </div>
                </div>
            </section>
    );
}