import React from 'react';
import { default_icon, default_title, default_content } from '../../constants';


export function FeatureBox({icon_class=default_icon, title=default_title, content=default_content}) {
    return (
        <div className="feature-box">
            <i className={`feature-box__icon ${icon_class}`}></i>
            <h3 className="heading-tertiary u-margin-bottom-small">{title}</h3>
            <p className="feature-box__text">
                {content}
            </p>
        </div>
    )
}