import React from 'react';

export function Card({ cardNumber = 1 }) {
    return (
        <div className="card">
            <div className={`card__side card__side--front card__side--front-${cardNumber}`}>
                <div className={`card__picture card__picture--${cardNumber}`}>
                    &nbsp;
                </div>

                <h4 className="card__heading">
                    <span className={`card__heading-span card__heading-span--${cardNumber}`}>
                        The Sea Explorer
                    </span>
                </h4>

                <div className="card__details">
                    <ul>
                        <li>3 day tour</li>
                        <li>Up to 30 people</li>
                        <li>2 Tour guides</li>
                        <li>Sleep in cozy hotels</li>
                        <li>Difficulty: Easy</li>
                    </ul>
                </div>
            </div>



            <div className={`card__side card__side--back card__side--back-${cardNumber}`}>
                <div className="card__cta">
                    <div className="card__price-box">
                        <p className="card__price-only">Only</p>
                        <p className="card__price-value">$297</p>
                    </div>
                    <a href="#" className="btn btn--white">Book Now!</a>
                </div>
            </div>

        </div>)
}